#container {
    height: 100vh;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;
    overflow-x: scroll;
}

#topContainer {
        color: white; 
        position: relative;
        margin-top: 100%;
        background-size: 100% auto;
        background-position: center;
        height: 40vh;
}

.topContainer #heading{
    top: 40%;
}

#arrow {
    position: absolute;
    width: 5%;
    left: 50%;
    transform: translateX(-50%);
    padding-bottom: 10%;
}

.aboutUs{
    color: black;
}

.aboutUsText {
    color: black;
    position: relative;
    top: 0;
}

.newsText {
    color: black;
    position: relative;
    top: 0;
}

.corporateText {
    color: black;
    position: relative;
    top: 0;
}


