/* up to tablet */
@media (max-width: 900px) {
    
    #navbar {
        /* position: fixed;
        display: flex;
        flex-direction: row;
        width: 100vw; */
        
        /* LEANNE CODE:
        dont need the above as your navbar is inside your  header which is already fixed :) */
    }
    
    #navbar ul {
        display: none;
        position: relative;
        -webkit-flex-direction: column;
                flex-direction: column;
        width: 100%;
        margin-top: 10%;
        /* right: 5%; - LEANNE CODE: this is forcing the ul to be off center */
        padding: 0;
    }

    .navbar ul {
        display: none;
    }
    
    .active ul {
        display: block !important;
    }

    .navbar li {
        -webkit-flex-direction: column;
                flex-direction: column;
        text-align: center;
        padding: 5%;
    }

    .burgerIcon {
        position: relative;
        display: block;
        border: none;
        width: 35px;
        height: 35px;
        border-radius: 0;
        left: 26px;
        top: 0px;
        background: #0E141D;
    }

    .burgerIcon .line,
    .burgerIcon .line:after,
    .burgerIcon .line:before{
        position: relative;
        display: block;
        width: 100%;
        height: 2px;
        background: white;
        transition: ease 0.5s;
    }

    .burgerIcon .line:after{
        content:'';
        top: -14px;
    }

    .burgerIcon .line:before{
        content:'';
        top: 12px;
    }

    .active .burgerIcon .line:after {
        -webkit-transform: translateY(12px);
                transform: translateY(12px)
    }

    .active .burgerIcon .line:before {
        -webkit-transform: translateY(-14px);
                transform: translateY(-14px)
    }
}




